.loader {
    position: fixed;
    top: 0px;
    left:0px;
    bottom: 0px;
    right: 0px;
    margin: auto;
    -webkit-animation: hue 4s linear infinite;
            animation: hue 4s linear infinite;
  }
  
  @-webkit-keyframes hue {
    100% {
      -webkit-filter: hue-rotate(360deg);
              filter: hue-rotate(360deg);
    }
  }
  
  @keyframes hue {
    100% {
      -webkit-filter: hue-rotate(360deg);
              filter: hue-rotate(360deg);
    }
  }
  
  .loader circle {
    -webkit-animation: stroke 1.4s ease infinite;
            animation: stroke 1.4s ease infinite;
  }
  
  @-webkit-keyframes stroke {
    0% {
      stroke-dasharray: 10, 84;
    }
    50% {
      stroke-dasharray: 84, 10;
      stroke-dashoffset: 0;
    }
    100% {
      stroke-dasharray: 10, 84;
      stroke-dashoffset: -94;
    }
  }
  
  @keyframes stroke {
    0% {
      stroke-dasharray: 10, 84;
    }
    50% {
      stroke-dasharray: 84, 10;
      stroke-dashoffset: 0;
    }
    100% {
      stroke-dasharray: 10, 84;
      stroke-dashoffset: -94;
    }
  }


